import React from "react";
import { StyleSheet, Text, View } from "react-native";

import { composeVowel } from "./index";

function Letter({ letterName, vowelPointName, isDagesh, style }) {
  return (
    <View style={styles.container}>
      <Text style={[styles.letter, style]}>
        {composeVowel({ letterName, vowelPointName, isDagesh: !!isDagesh })}
      </Text>
    </View>
  );
}

const styles = StyleSheet.create({
  letter: {
    fontSize: 10,
    color: "white",
  },
  container: { flex: 1, alignContent: "center", justifyContent: "center" },
});

export { Letter };
