import React, { useState } from "react";
import { View, StyleSheet } from "react-native";
import { Picker } from "@react-native-picker/picker";
import {
  letterNames,
  LetterWithSound,
  vowelPointNames,
  composeVowel,
} from "../../props/letters";
import { Keyboard } from "../../props/keyboard";

export function ChooseLetter() {
  const [letterName, setLetterName] = useState(letterNames.alef);
  const [vowelPointName, setVowelPointName] = useState(vowelPointNames.none);
  const [isDagesh, setIsDagesh] = useState(false);
  console.log(letterName, vowelPointName, isDagesh);

  const vowelList = Object.keys(vowelPointNames).map((vowelPointName) => {
    return (
      <Picker.Item
        label={composeVowel({ letterName, vowelPointName })}
        value={vowelPointName}
        key={vowelPointName}
      />
    );
  });

  return (
    <View style={styles.container}>
      <View style={[styles.keyboard]}>
        <Keyboard
          letterName={letterName}
          vowelPointName={vowelPointName}
          isDagesh={isDagesh}
          onPress={setLetterName}
        />
      </View>
      <View
        style={{
          flex: 1,
          flexDirection: "row-reverse",
        }}
      >
        <View
          style={{
            flex: 1,
          }}
        >
          <View style={[{ direction: "ltr", flex: 1, padding: 10 }]}>
            <Picker
              selectedValue={vowelPointName}
              style={styles.picker}
              onValueChange={(itemValue) => {
                console.log("itemValue", itemValue),
                  setVowelPointName(itemValue);
              }}
            >
              {vowelList}
            </Picker>
          </View>
        </View>
        <View
          style={{
            flex: 4,
          }}
        ></View>
      </View>

      <View style={styles.letter}>
        <LetterWithSound
          letterName={letterName}
          vowelPointName={vowelPointName}
          style={styles.font}
        />
      </View>
      <View style={styles.footer} />
    </View>
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    width: "100%",
  },
  keyboard: {
    flex: 5,
  },
  picker: {
    height: "100%",
    width: "100%",
    fontSize: "100%",
    color: "#ffffffc4",
    borderWidth: 0,
    alignItems: "center",
    elevation: 8,
    backgroundColor: "#53859d",
    borderRadius: 8,
    paddingVertical: 10,
    paddingHorizontal: 12,
    margin: 2,
  },
  letter: {
    flex: 1,
    justifyContent: "center",
    alignItems: "center",
  },
  footer: {
    flex: 1,
  },
  font: {
    fontSize: "700%",
  },
});
