import React from "react";
import { View, StyleSheet, TouchableHighlight, Text } from "react-native";
import { letters, letterNames } from "../letters";

const LetterButton = ({ letterName, onPress }) => {
  return (
    <View style={[styles.letterButton]}>
      <TouchableHighlight onPress={() => onPress && onPress(letterName)}>
        <View style={styles.button}>
          <Text style={styles.buttonText}>{letters[letterName]}</Text>
        </View>
      </TouchableHighlight>
    </View>
  );
};

function Keyboard({ onPress }) {
  return (
    <View style={styles.container}>
      <View style={[styles.row]}>
        <LetterButton onPress={onPress} letterName={letterNames.alef} />
        <LetterButton onPress={onPress} letterName={letterNames.bet} />
        <LetterButton onPress={onPress} letterName={letterNames.gimel} />
        <LetterButton onPress={onPress} letterName={letterNames.dalet} />
        <LetterButton onPress={onPress} letterName={letterNames.he} />
      </View>
      <View style={[styles.row]}>
        <LetterButton onPress={onPress} letterName={letterNames.vav} />
        <LetterButton onPress={onPress} letterName={letterNames.zayin} />
        <LetterButton onPress={onPress} letterName={letterNames.chet} />
        <LetterButton onPress={onPress} letterName={letterNames.tet} />
        <LetterButton onPress={onPress} letterName={letterNames.yod} />
      </View>
      <View style={styles.row}>
        <LetterButton onPress={onPress} letterName={letterNames.kaf} />
        <LetterButton onPress={onPress} letterName={letterNames.lamed} />
        <LetterButton onPress={onPress} letterName={letterNames.mem} />
        <LetterButton onPress={onPress} letterName={letterNames.nun} />
        <LetterButton onPress={onPress} letterName={letterNames.samech} />
      </View>
      <View style={styles.row}>
        <LetterButton onPress={onPress} letterName={letterNames.ayin} />
        <LetterButton onPress={onPress} letterName={letterNames.pe} />
        <LetterButton onPress={onPress} letterName={letterNames.tsadik} />
        <LetterButton onPress={onPress} letterName={letterNames.qof} />
        <LetterButton onPress={onPress} letterName={letterNames.resh} />
      </View>
      <View style={[styles.row]}>
        <LetterButton onPress={onPress} letterName={letterNames.shin} />
        <LetterButton onPress={onPress} letterName={letterNames.tav} />
        <View style={[styles.letterButton]} />
        <View style={[styles.letterButton]} />
        <View style={[styles.letterButton]} />
      </View>
      <View style={[styles.row]}></View>
    </View>
  );
}

export { Keyboard };

const styles = StyleSheet.create({
  container: {
    flex: 1,
    width: "100%",
  },
  row: {
    flexDirection: "row-reverse",
    alignContent: "space-between",
    width: "100%",
    height: 50,
  },
  letterButton: {
    flex: 1,
    marginLeft: 6,
    marginRight: 6,
    height: 50,
  },
  vowelPointButton: {
    flex: 1,
    marginLeft: 6,
    marginRight: 6,
    height: 50,
  },
  button: {
    elevation: 8,
    backgroundColor: "#2229f9",
    borderRadius: 8,
    paddingVertical: 10,
    paddingHorizontal: 12,
    margin: 2,
  },
  buttonText: {
    fontSize: "100%",
    backgroundColor: "transparent",
    color: "#ffffffc4",
    fontWeight: "bold",
    alignSelf: "center",
    textTransform: "uppercase",
  },
});
