import React, { useState } from "react";
import { StyleSheet, View, Image, Alert } from "react-native";
import { LetterWithSound, Letter, vowelPointNames } from "../../props/letters";
import { config } from "./config";

const { imageName, options, word } = config;
const hiddenKey = getHiddenKey(config);

function generateKey({ letterName, vowelPointName, isDagesh }) {
  return `${letterName}-${vowelPointName}-${isDagesh}`;
}

function getHiddenKey({ options, word }) {
  const optionKeys = options.map((option) => generateKey(option));
  const wordKeys = word.map((letter) => generateKey(letter));
  const hiddenKey = wordKeys.find((key) => optionKeys.indexOf(key) !== -1);
  return hiddenKey;
}

const shuffle = (array) => {
  var currentIndex = array.length,
    randomIndex;

  // While there remain elements to shuffle...
  while (0 !== currentIndex) {
    // Pick a remaining element...
    randomIndex = Math.floor(Math.random() * currentIndex);
    currentIndex--;

    // And swap it with the current element.
    [array[currentIndex], array[randomIndex]] = [
      array[randomIndex],
      array[currentIndex],
    ];
  }

  return array;
};

const Options = ({ onClick }) => {
  return shuffle(
    options.map((option) => {
      const key = generateKey(option);
      const { letterName, vowelPointName, isDagesh } = option;
      return (
        <View key={key} style={[styles.debug, styles.option]}>
          <LetterWithSound
            style={{ fontSize: 30 }}
            letterName={letterName}
            vowelPointName={vowelPointName}
            isDagesh={isDagesh}
            onClick={() => onClick({ key })}
          ></LetterWithSound>
        </View>
      );
    })
  );
};

const Word = () => {
  return word.map((letter) => {
    const { letterName, vowelPointName, isDagesh } = letter;
    const key = generateKey(letter);
    const isHidden = key === hiddenKey;
    const style = [styles.debug, styles.word];
    return isHidden ? (
      <View key={key} style={style}>
        <View style={{ fontSize: 30 }}></View>
      </View>
    ) : (
      <View key={key} style={style}>
        <Letter
          style={{ fontSize: 30 }}
          letterName={letterName}
          vowelPointName={vowelPointName}
          isDagesh={isDagesh}
        ></Letter>
      </View>
    );
  });
};

const MissingLetter = () => {
  const handleClick = ({ key }) => {
    console.log(key);
    if (key === hiddenKey) {
      console.log("bingo");
      if (window.alert) {
        window.alert("Bingo!");
      }
    }
  };

  return (
    <View style={[styles.debug, styles.container]}>
      <View style={[styles.debug, styles.header]}></View>
      <View style={[styles.debug, styles.imageContainer]}>
        <Image
          style={styles.image}
          source={require(`../../assets/images/${imageName}`)}
        />
      </View>
      <View style={[styles.debug, styles.optionsContainer]}>
        <Options onClick={handleClick} />
      </View>
      <View style={[styles.debug, styles.wordContainer]}>
        <View style={[styles.debug, styles.wordInnerContainer]}>
          <Word />
        </View>
      </View>
    </View>
  );
};

const styles = StyleSheet.create({
  debug: {
    borderColor: "white",
    borderWidth: 3,
    margin: 2,
  },
  container: {
    flex: 1,
    width: "100%",
  },
  header: {
    flex: 1,
  },
  imageContainer: {
    flex: 2,
  },
  optionsContainer: {
    flex: 1,
    flexDirection: "row",
  },
  option: {
    flex: 1,
    margin: 10,
    alignItems: "center",
    justifyContent: "center",
  },
  wordContainer: {
    flex: 1,
    flexDirection: "row-reverse",
    justifyContent: "center",
  },
  wordInnerContainer: {
    flex: 1,
    flexDirection: "row-reverse",
    justifyContent: "center",
    marginHorizontal: 50,
  },
  word: {
    flex: 1,
    margin: 0,
    alignItems: "center",
    justifyContent: "center",
  },
  image: {
    flex: 1,
    resizeMode: "center",
  },
});
export { MissingLetter };
