import {
  dagesh,
  soundMap,
  vowelPoints,
  letters,
  isDageshSound,
} from "./constants";

const composeVowel = ({ letterName, vowelPointName, isDagesh }) => {
  const letter = letters[letterName];
  const vowelPoint = vowelPoints[vowelPointName];
  const _dagesh = (isDagesh && dagesh) || "";
  return `${letter}${_dagesh}${vowelPoint}`;
};

const composeSoundFileName = ({ letterName, vowelPointName, isDagesh }) => {
  const _dagesh = (isDagesh && isDageshSound[letterName] && "dagesh") || null;
  const sound =
    vowelPointName && vowelPointName !== "none"
      ? `${soundMap[vowelPointName]}`
      : null;
  return `${[letterName, _dagesh, sound]
    .filter((entry) => entry)
    .join("-")}.m4a`;
};

export { composeSoundFileName, composeVowel };
