import React from "react";
import { StyleSheet, Text, View } from "react-native";
import { SoundOnPress } from "../../sound/index";
import { composeSoundFileName, composeVowel } from "./index";

function LetterWithSound({
  letterName,
  vowelPointName,
  isDagesh,
  style,
  onClick,
}) {
  return (
    <SoundOnPress
      soundFile={composeSoundFileName({
        letterName,
        vowelPointName,
        isDagesh: !!isDagesh,
      })}
      afterSound={onClick}
      style={styles.container}
    >
      <Text style={[styles.letter, { color: getRandomColor() }, style]}>
        {composeVowel({ letterName, vowelPointName, isDagesh: !!isDagesh })}
      </Text>
    </SoundOnPress>
  );
}

function getRandomColor() {
  var letters = "0123456789ABCDEF";
  var color = "#";
  for (var i = 0; i < 6; i++) {
    color += letters[Math.floor(Math.random() * 16)];
  }
  return color;
}

const styles = StyleSheet.create({
  letter: {
    fontSize: 10,
    textAlign: "center",
  },
  container: {
    flex: 1,
    width: "100%",
    alignItems: "center",
    justifyContent: "center",
  },
});

export { LetterWithSound };
